.tree-view_children {
	margin-left: 16px;
	color: gray;
}

.tree-view_children-collapsed {
	height: 0px;
}

.tree-view_arrow {
	cursor: pointer;
	margin-right: 6px;
	display: inline-block;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	color: black;
}

.tree-view_arrow:after {
	content: "▾";
	color: black;
}

/* rotate the triangle to close it */
.tree-view_arrow-collapsed {
	transform: rotate(-90deg);
}

